<template>
  <base-layout>
    <div class="content text-center">
      <h2 class="my-30px">卡片市场价值估算:</h2>
      <p>
        当前 <strong> 1bnx = {{ goldRatio }} gold </strong>
      </p>

      <div>
        <a-input-search
          style="width: 500px"
          placeholder="输入 token_id"
          enter-button="价值估算"
          size="large"
          v-model="tokenId"
          @search="calValueFromTokenId"
        />
      </div>
      <br />
      <props-table :loading="loading" :data-source="propsDataSource" />
      <br>
      <price-table :loading="priceLoading" :data-source="priceDataSource" />
    </div>
  </base-layout>
</template>
<script>
import mixins from "../mixins";
import config from "../config";
import PropsTable from "../components/market/propsTable.vue";
import PriceTable from "../components/market/priceTable.vue";
import { getCareerText } from "../utils";

const blockCount = 28800

export default {
  name: "MarketModel",
  mixins: [mixins],
  components: {
    PropsTable,
    PriceTable,
  },
  data() {
    return {
      goldRatio: 0,
      tokenId: "",
      loading: false,
      startDay: 29,
      endDay: 39,
      priceLoading: false,
      propsDataSource: [],
      priceDataSource: []
    };
  },
  async created() {
    await this.initWeb3Account();
    if (!this.account) {
      return;
    }

    this.initContract();
    await this.getGoldBnxRatio();
    // await this.calValueFromTokenId(
    //   "22232475907033038250301944470763415820037487937383552587412860443721291355267"
    // );
  },
  methods: {
    // 根据 token_id 估算价格
    async calValueFromTokenId(tokenId) {
      if (!tokenId) {
        return;
      }
      this.loading = true;
      const data = await this.playContract.methods
        .getPlayerInfoBySet(tokenId)
        .call();
      this.loading = false;

      if (data) {
        const propsData = {
          strength: data[0][0],
          agility: data[0][1],
          physique: data[0][2],
          volition: data[0][3],
          brains: data[0][4],
          charm: data[0][5],
          level: data[0][6],
          career: getCareerText(data[1]),
        };

        // 每区块收益
        const valPerBlock = this.calValPerBlock(propsData);
        const valPerDay = valPerBlock * blockCount
        propsData.valPerBlock = `${valPerBlock.toFixed(4)} gold`
        propsData.valPerDay = `${valPerDay.toFixed(2)} gold`
        propsData.cardType = this.getCardType(propsData);
        console.log(this.getCardType(propsData))

        this.propsDataSource = [];
        this.propsDataSource.push(propsData);
        // 每日收益
        this.priceLoading = true
        // 回本数据
        this.priceDataSource = this.calPriceInDays(valPerDay)
        this.priceLoading = false
      }
    },
    getCardType({
                  strength,
                  agility,
                  physique,
                  brains,
                  charm,
                  career,
                }){
      let conditionProps = [];
      switch (career) {
        case "法师":
          conditionProps = [brains, charm]
          break;
        case "战士":
          conditionProps = [strength, physique]
          break;
        case "游侠":
          conditionProps = [strength, agility]
          break;
        case "盗贼":
          conditionProps = [agility, strength]
          break;
      }
      if (conditionProps[0] >= 86 && conditionProps[1] >= 61) {
        return '矿卡'
      } else {
        return '黑卡'
      }
    },
    calPriceInDays(valPerDay) {
      const arr = []
      for (let day = this.startDay; day <= this.endDay; day++) {
        arr.push({
          day,
          price: `${((day * valPerDay) / this.goldRatio).toFixed(2)} bnx`,
          valPerDay: `${valPerDay.toFixed(4) } gold`
        })
      }
     return arr
    },
    // 计算每区块收益
    calValPerBlock({
      strength,
      agility,
      physique,
      brains,
      charm,
      level,
      career,
    }) {
      let mainProp = "";
      let conditionProps = [];

      switch (career) {
        case "法师":
          mainProp = brains;
          conditionProps = [brains, charm]
          break;
        case "战士":
          mainProp = strength
          conditionProps = [strength, physique]
          break;
        case "游侠":
          mainProp = strength;
          conditionProps = [strength, agility]
          break;
        case "盗贼":
          mainProp = agility;
          conditionProps = [agility, strength]
          break;
      }

      // 满足等级和属性要求
      if (level > 1 && conditionProps[0] >= 86 && conditionProps[1] >= 61) {
        return this.calVal(mainProp, level);
      } else {
        return this.calVal(85, level);
      }
    },
    // 计算每区块收益
    calVal(mainProp, level) {
      const basicVal = 0.01;
      const extraVal = (mainProp - 85) * 0.005;

      if (level > 1) {
        return (basicVal + extraVal) * Math.pow(2, level - 1);
      }
      return basicVal;
    },
    async getGoldBnxRatio() {
      const inputGold = new this.web3.utils.BN("10000000000000000000");
      const outPut = await this.pancakeContract.methods
        .getAmountsOut(inputGold, [
          config.bnxContrAddr,
          config.busdContrAddr,
          config.goldContrAddr,
        ])
        .call();
      const gold = new this.web3.utils.BN(outPut[2]);
      const ratio = gold.div(this.web3.utils.toBN(10));
      console.log(ratio);
      this.goldRatio = this.web3.utils.fromWei(ratio);
    },
  },
};
</script>

<style scoped></style>
