<template>
  <a-table
    bordered
    :loading="loading"
    :columns="tableColumns"
    :data-source="dataSourceWithKey"
    :scroll="{ x: true }"
    :pagination="false"
  >
  </a-table>
</template>

<script>
import { genUniqueKey } from "../../utils/index";

export default {
  props: {
    dataSource: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    tableColumns() {
      return [
        {
          title: "回本天数",
          width: 90,
          dataIndex: "day",
        },
        {
          title: "售价",
          width: 90,
          dataIndex: "price",
        },
        {
          title: "每日收益",
          width: 90,
          dataIndex: "valPerDay",
        },
      ];
    },
    dataSourceWithKey() {
      return this.dataSource.map((d) => ({ ...d, key: genUniqueKey() }));
    },
  },
};
</script>
